@tailwind base;

@tailwind components;

@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

body, * {
	font-family: 'Roboto', sans-serif;
}

.tooltip-wrapper {
	@apply relative inline-block;

	.tooltip {
		@apply transition absolute opacity-0 pointer-events-none p-2 bg-gray-900 text-white rounded text-sm;
		bottom: calc(100% + 0.6em);
		left: 50%;
		transform: translateX(-50%);

		&::before {
			@apply block absolute bg-gray-900 w-2 h-2;
			content: "";
			top: 100%;
			left: 50%;
			transform: translate(-50%,-50%) rotate(45deg);
		}
	}

	&:hover, &:active {
		.tooltip {
			@apply opacity-100 delay-1000;
		}
	}
}

.info-wrapper {
	@apply inline-block;
	font-size: 1rem;

	.info {
		@apply transition flex justify-center items-center border border-gray-900 rounded-full pointer-events-none;
		width: 1.2em;
		height: 1.2em;
	}

	.tooltip-wrapper:hover .info {
		@apply text-white bg-gray-900
	}

	.tooltip {
		transition-delay: 0ms !important;
	}
}

.ari-type-checkbox,
.ari-type-radio {
	& > div {
		@apply flex flex-wrap;
	}

	.ari-touchable {
		@apply w-full sm:w-1/2 md:w-1/3;
	}
}

@keyframes bounce {
	0%, 100% {
		transform: translateY(100%);
		animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
	}
	50% {
		transform: translateY(0);
		animation-timing-function: cubic-bezier(0.8, 0, 1, 1);
	}
}
